@import url('https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,500;1,300&display=swap');

*{
  margin: 0;
}

body{
  height: 100vh;
  width: 100vw;
  background-image: url('./assets/Papeldeparede1.jpg');
}

.Header{
  background-color: #fff;
  position: relative;
  text-align: center;
  height: auto;
  width: fit-content;
  font-weight: 300;
  padding: 15px 25px;
  border-radius: 30px;
  margin-left: auto;
  margin-right: auto;
  font-family: 'FontLove', cursive;
  font-size: 25px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.cardPresentes{
  background-image: url('./assets/33513eebf2.jpg');
  height: 200px;
  padding: 8px;
  width: 95%;
  border-radius: 30px;
  box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
  position: relative;

}

.cardPresentes:hover{
  scale: 1.1;
}


.cardPresentes img{
  max-width: 70%;
  max-height: 70%;
  position: absolute;
  top: 20px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.cardPresentes h3{
  background-color: #fff;
  position: absolute;
  bottom: 5%;
  padding: 2px 20px;
  border-radius: 30px;
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

}

@font-face {
  font-family: 'FontLove';
  src: url('./assets/Fontes/Bobby\ Jones\ Soft.otf');
}